import React, { Component } from 'react'

export class Item extends Component {
  render() {
      let {name,company} = this.props;
    return (
          <tr>
                <th scope="row">{company}c</th>
                <td>{name}n</td>
                <td>None</td>
                <td>None</td>
                {/* <td>None</td> */}
        </tr>
      
    )
  }
}

export default Item