import React, { Component } from 'react'
import Item from './Item'
import './Table.css'

export class Tables extends Component {
    dictionary = {
        "clients": [
             { 
                "name": "Nandha",
                "id" : "1",
                "company" : "BooksApp",
                "orderId": "1", 
                "invoicepaid": "250",
                "invoicePending": "280"
            },
            { 
                "name": "Ashley",
                "id" : "2",
                "company" : "NotarizedDocs",
                "orderId": "13", 
                "invoicepaid": "150",
                "invoicePending": "180"
            },
            { 
                "name": "Rajal",
                "id" : "4",
                "company" : "Conneqtly",
                "orderId": "14", 
                "invoicepaid": "500",
                "invoicePending": "580"
            }
        ]
     }

     dicList = this.dictionary.clients

     constructor(){
         super();
         this.state={
            dicList:[]
         }
     }
     async componentDidMount(){
         let url = "https://run.mocky.io/v3/8260aa5d-8af8-4cff-999e-6e81b217f0ba"
         let data = await fetch(url)
         let parseData = await data.json()
         this.setState({dicList:parseData.clients})
     }
     
  render() {
    return (
      <div>
          <div className='light'> Light Table</div>
          <table className="table">
            <thead>
                <tr className='light-down'>
                <th scope="col">COMPANY</th>
                <th scope="col">NAME</th>
                <th scope="col">STATUS</th>
                <th scope="col">USERS</th>
                {/* <th scope="col">COMPLETION</th> */}
                </tr>
            </thead>
            <tbody>
                {this.state.dicList.map((element)=>{
                    return <Item key={element.id?element.id:""} company={element.company?element.company:""} name={element.name?element.name:""}/>
                })}
            </tbody>
        </table>
      </div>
    )
  }
}

export default Tables